[class][class].BuildTable-Row {
  cursor: pointer; }
  [class][class].BuildTable-Row_building {
    background-color: var(--building-background); }
    [class][class].BuildTable-Row_building:hover {
      background-color: var(--building-background-hover); }
  [class][class].BuildTable-Row_fail {
    background-color: var(--fail-background); }
    [class][class].BuildTable-Row_fail:hover {
      background-color: var(--fail-background-hover); }
  [class][class].BuildTable-Row_success {
    background-color: var(--success-background); }
    [class][class].BuildTable-Row_success:hover {
      background-color: var(--success-background-hover); }
